@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;700&display=swap);
:root{
  --blue-color: #292361;
  --dark-blue-color: #544F81
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: 'Quicksand', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 30px auto 0;
  text-align: center;
  max-width: 600px;
  animation: transitionOpacity 0.7s both;
}

input[type="text"] {
  width: 100%;
  font-size: 20px;
  padding: 5px 10px;
  margin: 20px 0;
}

label[for="add"] {
  font-size: 22px;
  font-weight: bold;
}

.date {
  margin: 50px 0;
}

label[for="date"] {
  margin-right: 20px;
}

.form button,
.welcome-panel button,
.error button {
  padding: 10px 30px;
  text-transform: uppercase;
  background-color: var(--blue-color);
  color: #fff;
  border: none;
  border-radius: 20px;
  font-size: 20px;
  cursor: pointer;
}

.task{
    flex-basis: 45%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
    margin: 2.5%;
    border: 2px solid #F1F1F1;
    border-radius: 30px;
    text-align: center;
}

.task__title{
    font-size: 18px;
}

.task__date{
    margin: 15px 0;
}

.task__confirmation{
    margin-bottom: 5px;
    border-bottom: 1px solid;
}

.task__btns button{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    padding: 5px 10px;
    margin: 10px 0;
    border: none;
}
button{
    cursor: pointer;
}
button .fas{
    margin-right: 4px;
}
.fa-check-circle{
    font-size: 18px;
    color: green
}
.fa-times{
    font-size: 18px;
    color: rgb(175, 0, 0)
}
@media(min-width: 1024px){
    .task{
        flex-basis: 28%;
    }
}
.active-tasks, .done-tasks{
    padding: 20px 0;
    animation: transitionOpacity .7s both;
}
.priotity{
    margin-bottom: 20px;
    
}
.priority__title, .not-priority__title{
    display: inline-block;
    font-size: 24px;
    font-weight: bold;
    margin: 0 20px 20px 0;
}
.priority__counter{
    color: #B8B8B8;
}
.line{
    display: block;
    width: 50%;
    transition: .3s;
}
.line--red{
    border-bottom: 3px solid #FE6665;
}
.line--blue{
    border-bottom: 3px solid #6471FE
}

.priority__title:hover .line, .not-priority__title:hover .line {
    width: 100%;
}
.active-tasks__container{
    display: flex;
    flex-wrap: wrap;
}
.navigation{
    position: fixed;
    display: flex;
    justify-content: space-around;
    align-items: center;
    bottom: 0;
    left: 0;
    right: 0;
    height: 70px;
    padding: 0 20px;
    background-color: var(--blue-color)
}

.navigation a{
    background-color: transparent;
    border: none;
    width: 36px;
    height: 36px;
    transition: .2s;
}

.navigation .tasks{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    width: 40px;
    height: 40px;
}

.navigation .tasks span{
    height: 14px;
    width: 14px;
    border-radius: 4px;
    background-color: var(--dark-blue-color);
}
.navigation .add-task{
    position: relative;
    background-color: var(--dark-blue-color);
    border-radius: 10px;
}
.add-task span{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    height: 3px;
    width: 45%;
    border-radius: 10px;
    background-color: var(--blue-color);
}
.add-task span:nth-of-type(2){
    transform: translate(-50%,-50%) rotate(90deg);
}
.navigation .done-tasks{
    position: relative;
    background-color: var(--dark-blue-color);
    border-radius: 50%;
}

.navigation .done-tasks::after{
    content: '';
    position: absolute;
    right: -5px;
    top: -5px;
    height: 20px;
    width: 20px;
    background-color: #544F81;
    border-bottom: 4px solid var(--blue-color);
    border-left: 4px solid var(--blue-color);
    border-radius: 0 150% 0 8px;
}

.navigation .tasks.active{
    background-color: transparent
}
.navigation .active, 
.navigation .active::after, 
.navigation .tasks.active span {
    background-color: #fff;
}
@media(min-width: 768px){
    .navigation{
      top: 0;
      width: 70px;
      height: auto;
      flex-direction: column;
      justify-content: flex-start;
    }
    .navigation a{
        margin: 80px 0;
    }
  }
header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 150px;
}

header img{
    height: 120px;
}
header h2{
    font-size: 16px;
    text-align: right;
}

@media(min-width: 1024px){
    header h1{
        font-size: 42px;
    }
}
.welcome-panel{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: calc(100vh - 150px - 70px - 20px );
    animation: transitionOpacity 1s both;
}

.welcome-panel img{
    width: 100%;
    margin-bottom: -100px;
}

.welcome-panel p{
    font-size: 13px;
}
@media(min-width: 768px){
    .welcome-panel img{
        width: 550px;
    }
}

@keyframes transitionOpacity {
    0%{opacity: 0;}
    100%{opacity: 1;}
}
.error {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 20%;
}

.error img {
  width: 100%;
  margin-bottom: 10%;
}
.error button {
  margin-top: 10%;
  font-size: 14px;
}
@media (min-width: 768px) {
  .error img {
    width: 550px;
  }
  .error {
    padding-top: 2%;
  }
  .error button {
    font-size: 20px;
  }
}

.App {
  padding: 20px 20px 70px;
  max-width: 1300px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .App {
    padding: 20px 20px 20px 100px;
  }
}

